<template>
    <b-container fluid>
      <pre>{{product}}</pre>
        <b-container fluid class="mx-auto text-center">
            <p class="ml-4 mt-2 mb-n1">{{ product.name }}</p>
            <b-form-rating inline no-border
                variant="warning"
                readonly
                v-model="product.rating"
                precision="2">
            </b-form-rating>
        </b-container>
        <b-container fluid>
            <b-row>
                <b-col cols="12" md="6">
                    <b-carousel
                        id="carousel-1"
                        :interval="4000"
                        controls
                        indicators
                        img-width="200"
                        img-height="200"
                        background="gray"
                        style="text-shadow: 1px 1px 2px #333;"
                        @sliding-start="onSlideStart"
                        @sliding-end="onSlideEnd"
                    >
                      <b-carousel-slide
                        v-for="(image,index) in product.images" :key="index">
                        <template v-slot:img>
                          <img width="200"
                            height="300"
                            background-color="red"
                            :src=image.link
                            class="d-block w-100"
                          >
                        </template>
                      </b-carousel-slide>
                    </b-carousel>
                </b-col>
                <b-col cols="12" md="6" class="d-flex flex-column mx-auto mt-2 mt-md-0" v-if="productLoaded">
                    <div>Specifications:</div>
                    <ul v-for="([s,values],i) in Object.entries(product.specifications)" :key="i">{{s}}
                        <span v-if="Array.isArray(values)">
                        <b-list-group v-for="(spec,index) in values" :key="index" class="ml-5">
                            <li>{{spec}}</li>
                        </b-list-group>
                        </span>
                        <span v-else><li class="ml-5">{{values}}</li></span>
                    </ul>
                    <!-- <div>Details:</div>
                    <div class="d-flex flex-column">
                        <div class="ml-5">
                            <ul>
                                <li>Indian</li>
                                <li>100% cotton</li>
                            </ul>
                        </div>
                    </div> -->
                    <div>Price:</div>
                    <div class="d-flex flex-column">
                        <div class="ml-5">
                            <span id="prev-price">
                                <strike>&#2547;{{ Math.round(product.price.sellingPrice / (1 -  product.price.discount/ 100)) }}</strike>
                                <strong id="discount">{{ product.price.discount*(-1) }}%</strong>
                            </span>
                            <br>
                            <span id="cur-price">&nbsp;&nbsp;&#2547;{{product.price.sellingPrice}}</span>
                        </div>
                    </div>
                    <div>Warranty:</div>
                    <div class="d-flex flex-column">
                        <div class="ml-5">
                            <ul>
                                <li v-if="product.price.warranty!=null">{{product.price.warranty}} days return</li>
                                <li v-if="product.price.refundable">100% refund</li>
                            </ul>
                        </div>
                    </div>
                </b-col>
            </b-row>
            <div class="mx-auto text-center col-md-4" v-if="show">
            <spec-selector :productId="product.productID" :product-init="product"></spec-selector>
                <!-- </b-button-group> -->
            </div>
            <hr>
            <div class="mx-auto bd-highlights text-center" v-if="show">
                <h3>Related Product</h3>
            </div>
            <!-- <b-row class="">
              <b-col v-for="(product, idx) in relatedProducts" :key="idx" lg="2" md="3" sm="4" xs="7" class="productClass">
                <product-box :product="product"></product-box>
              </b-col>
            </b-row> -->
        </b-container>
        <ProductScrollSection :products="relatedProducts"></ProductScrollSection>
    </b-container>
</template>
<script>
import ProductBox from '../../components/Boxes/ProductBox'
import ProductScrollSection from '../../components/ProductScrollSection.vue'
import specSelector from '../../components/SpecSelector.vue'
import { mapActions } from 'vuex'
import api from '../../plugins/axios'
import loader from '../../mixins/loader'

export default {
  name: 'CertainProduct',
  data () {
    return {
      // i: 0,
      // value: 3.5,
      // prevPrice: 500,
      // curPrice: 400,
      // discount: -20,
      // selected: null,
      // ProductName: 'Facebook',
      // coloroptions: ['red', 'green', 'yellow'],
      // sizeoptions: ['L', 'XL', 'XXL'],
      // images: [
      //   'https://static-01.daraz.com.bd/p/7883ac9ce3be09d4e7e9e44f139e8d68.jpg',
      //   'https://static-01.daraz.com.bd/p/7883ac9ce3be09d4e7e9e44f139e8d68.jpg'
      // ],
      // totalimage: 2,
      // value: 4,
      // from here
      show: false,
      sliding: null,
      product: {},
      productLoaded: false,
      relatedProducts: []
    }
  },
  components: {
    ProductScrollSection,
    specSelector,
    // eslint-disable-next-line vue/no-unused-components
    ProductBox
  },
  mixins: [
    loader
  ],
  methods: {
    ...mapActions([
      'addToCart'
    ]),
    onSlideStart (slide) {
      this.sliding = true
    },
    onSlideEnd (slide) {
      this.sliding = false
    },
    showPrev () {
      if (this.i === 0) this.i = this.totalimage
      this.i = (this.i - 1) % this.totalimage
    },
    showNext () {
      this.i = (this.i + 1) % this.totalimage
    },
    loadProduct (id) {
      this.loader('/product', { productID: id }, `product-${id}`)
        .then(response => {
          this.product = response.data
          this.product.specifications = this.product.extra
          this.productLoaded = true
          delete this.product.extra
          this.show = true
          this.loadRelatedProducts()
        })
    },
    loadRelatedProducts () {
      api.post('/search', { productIds: this.product.relatedProductIDs })
        .then(response => {
          console.log(`products related to ${this.product.productID} loaded`)
          console.log(response)
          this.relatedProducts = response.data.products
        })
        .catch(error => {
          console.log(`products related to ${this.product.productID} failed loading`)
          console.log(error.response)
        })
    }
  },
  watch: {
    '$route' (to, from) {
      this.loadProduct(to.params.id)
    }
  },
  created () {
    this.loadProduct(this.$route.params.id)
  }
}
</script>
<style>
    #prev-price {
        color: grey;
    }
    #discount {
        color: red;
    }
    #cur-price {
        font-size: 120%;
        color: darkorange;
    }

</style>
